.movie {
  display: flex;
  align-items: center;
  text-align: left;
  height: 400px;
}

.movie-pic {
  margin-right: 200px;
  margin-bottom: 40px;
  margin-top: 40px;
  position: relative;
  padding: 0px;
}

.movie-pic__genre {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.4);
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-weight: 400;
}

.hair-line {
  padding: 0px 30px;
  box-sizing: border-box;
}
